.Cart {
  -ms-flex: 1 auto 0%;
      flex: 1 auto 0%;
  width: 25%;
  padding-bottom: 20px;
}

.Cart-total {
  text-align: right;
  font-size: 1.5em;
  border-top: 1px solid #E0E0E0;
  padding-top: 5px;
  margin: 0 20px;
  color: #689F38;
}

.Cart-empty {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  background-color: #F5F5F5;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #9E9E9E;
  margin: 0 20px 10px 20px;
  margin-bottom: 10px;
  text-align: center;
}

.Cart-submit {
  background-color: #0D47A1;
  color: #FFF;
  padding: 10px;
  margin-top: 20px;
  border: 0;
  font-size: 0.8em;
  float: right;
  margin-right: 20px;
}

.Cart-submit:disabled {
  opacity: 0.3;
}

.Cart-title {
  padding-left: 15px;
  padding-top: 10px;
  margin-bottom: 40px;
}

@media (max-width: 800px) {
  .Cart {
    width: 100%;
  }
}



